import {
  mapState,
  mapMutations
} from 'vuex'
	export default {
		data() {
			return {
		    active: false, // 是否显示输入框
		    todoValue: '',
		    activeTabIndex: 0,
		    tabText: '代办',
		    textShow: false
			}
		},
    props: {
      list: {
        type: Array,
        required: true
      }
    },
		computed: {
      ...mapState(['tab']),
		  listData() {
		    const list = JSON.parse(JSON.stringify(this.list))
		    
		    // 点击全部
		    if (this.activeTabIndex === 0) {
          // checked = false
          this.tabText = '代办'
          return list.filter(item => !item.checked)
		    }
		    // 点击待办事项
		    if (this.activeTabIndex === 1) {
          // checked = true
          this.tabText = '已完成'
          return list.filter(item => item.checked)
		    }
		    // 点击已完成
		    if (this.activeTabIndex === 2) {
          this.tabText = '全部'
          return list
		    }
		    
		  }
		},
		methods: {
      ...mapMutations(['switchTab']),
		  switchTabHeader(index) {
		    this.activeTabIndex = index
        this.switchTab(index)
        console.log('tab=', index, this.tab)
		  }
		},
    mounted() {
      console.log(this.list)
    }
	}
